<template>
  <v-app id="inspire">
            <v-sheet
              min-height="50vh"
              rounded="lg"
            >
              <v-container>
                  <div class="mt-4 text-h4 text-center">
                  Add New Update
                </div>
                 <v-spacer></v-spacer>
                  <v-row class="mt-5" align="center"
      justify="center">
                  <v-col align-self="end" cols="10" sm="8">
                  <v-select v-model="select" label="Program" :items="$store.state.programs" outlined></v-select>
                </v-col>
                <v-col cols="10" align-self="end" sm="8">
                  <v-text-field
