                  label="Version"
                  outlined hint="ex. v1.0.5" persistent-hint
                ></v-text-field>
                </v-col>
                <v-col cols="10" align-self="end" sm="8">
                  <v-checkbox
              v-model="urge"
              label="Urgent"
              color="red"
              hide-details
            ></v-checkbox>
