  export default {
    data() {return{
      select:'',
      version:'',
      urge:false
    }},
    created(){
      this.$store.dispatch('getPrograms')
    },
    methods:{
        newU(){
            this.$store.dispatch('newUpdate',{program:this.select,version:this.version,urge:this.urge})
        }
    }
  }
