<template>
<div>
  <v-card>
    <v-card-title>
      Programs
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="this.progs"
      :items-per-page=8
      :search="search"
    >
    <template v-slot:top>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >

          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="10"
                    sm="8"
                    md="8"
                  >
                    <v-text-field
                      v-model="editedItem.prog"
                      label="Program"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="10"
                    sm="8"
                    md="8"
                  >
                    <v-text-field
                      v-model="editedItem.price"
                      label="Price"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
    </template>
    <template v-slot:item.control="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    </v-data-table>
  </v-card>

    <v-snackbar
      v-model="snack"
      :timeout="3000"
      :color="snackColor"
    >
      {{ snackText }}

      <template v-slot:action="{ attrs }">
        <v-btn
          v-bind="attrs"
          text
          @click="snack = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
    </div>
</template>
<script>
  export default {
    created(){
      this.initialize()
      this.$store.dispatch('getProgramsStat')
    },
    data () {
      return {
        snack: false,
        snackColor: '',
        snackText: '',
        dialog: false,
        dialogDelete: false,
        progs:this.$store.state.programs_stats,
        search: '',
        headers: [
          { text: 'Program', value: 'prog' },
          { text: 'Keys', value: 'keys' },
          { text: 'Valid', value: 'valids' },
          { text: 'Expires', value: 'expires' },
          { text: 'Price', value: 'price' },
          { text: 'Control', value: 'control' },
        ],
      editedIndex: -1,
      editedItem: {
        prog: '',
        keys: '',
        validis: '',
        expires: '',
        price: '',
      },
      defaultItem: {
        prog: '',
        keys: '',
        validis: '',
        expires: '',
        price: '',
      },
      }
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch:{
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
      progs(){
        this.progs = this.$store.state.programs_stats
      }
    },
    methods:{
      initialize () {
        this.progs = this.$store.state.programs_stats

      },
       editItem (item) {
        this.editedIndex = this.progs.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        this.editedIndex = this.progs.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.$store.dispatch('deleteProg',{program:this.editedItem.prog})
        this.progs.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      save () {
        if (this.editedIndex > -1) {
          this.$store.dispatch('editProg',{prog:this.progs[this.editedIndex].prog,price:this.progs[this.editedIndex].price})
          Object.assign(this.progs[this.editedIndex], this.editedItem)
        } else {
          this.progs.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>