<template>
  <v-card>
    <v-card-title>
      Keys
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="this.keys"
      :items-per-page=8
      :search="search"
    >
    <template v-slot:item.control="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    </v-data-table>
  </v-card>
</template>
<script>
  export default {
    created(){
      this.$store.dispatch('getKeysStat')
      this.keys = this.$store.state.keys
    },
    data () {
      return {
        keys:this.$store.state.keys,
        search: '',
        headers: [
          { text: 'Key', value: 'key' },
          { text: 'Program', value: 'program' },
          { text: 'Mac', value: 'mac' },
          { text: 'Status', value: 'status' },
          { text: 'Expire in', value: 'expire' },
          { text: 'Last Used', value: 'last_used' },
          { text: 'Control', value: 'control' },
        ]
      }
    },
    watch:{
      keys(){
        this.keys = this.$store.state.keys
      }
    },
    methods:{
      deleteItem(item){
      this.$store.dispatch('deleteKey',{keys:item.key})
      this.keys.splice(this.keys.findIndex(a => a.KeyStr === item.key) , 1)
      }
    }
  }
</script>