<template>
  <v-app id="inspire">
            <v-sheet class="mt-15"
              min-height="50vh"
              rounded="lg"
            >
              <v-container >
                  <div class="mb-10 text-h5 text-center">
                  Welcome Again Mr Snowman To Your Safe Place
                </div>
                  <v-row justify="center" align="center">
               
                <v-col cols="10" align-self="end" sm="8">
                  <v-text-field
                  v-model="user"
                  label="Name"
                  outlined
                ></v-text-field>
                </v-col>
                <v-col cols="10" align-self="end" sm="8">
                  <v-text-field
                  v-model="pass"
                  label="Pass"
                  outlined
                  type="password"
                ></v-text-field>
                </v-col>
                <v-col class="text-center" justify="center" align-self="center" sm="8">
                  <v-btn 
                  class="ma-2"
                  color="secondary"
                  @click="login()"
                >
                  Login
                </v-btn>
                </v-col>
                </v-row>
              </v-container>
            </v-sheet>
  </v-app>
</template>

<script>
export default {
  data(){
    return {
      user:'',
      pass:''
    }
  },
  methods:{
    login(){
      this.$store.dispatch('login',{name:this.user,pass:this.pass})
    },
  },
  mounted(){
    if(this.$store.state.isLogged){
      this.$router.push('/')
    }
  }
}
</script>